.Card {
  margin-bottom: 36px; }
  .Card .Title {
    margin-top: 12px; }
    .Card .Title .Name {
      display: inline-block;
      background: #eee;
      height: 18px;
      width: 200px; }
    .Card .Title .Price {
      float: right;
      background: #eee;
      height: 18px;
      width: 46px; }
  .Card .Description {
    background: #eee;
    margin-bottom: 26px;
    height: 12px;
    width: 140px; }
  .Card .Tags {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between; }
    .Card .Tags .Tag {
      background: #eee;
      height: 12px;
      width: 30%; }
  @media screen and (min-width: 992px) {
    .Card {
      -webkit-flex-basis: calc(50% - 30px);
      -moz-flex-basis: calc(50% - 30px);
      -ms-flex-basis: calc(50% - 30px);
      flex-basis: calc(50% - 30px);
      margin-left: 15px; } }



